import React from 'react';
import { Route, Switch, Link } from 'react-router-dom';
import posed, { PoseGroup } from 'react-pose';
import { useTheme } from '../../hooks';
import { ToggleButton, Chat, MorphingIcon } from '../';
import './App.scss';
import Info from '../Info/Info';
const RouteContainer = posed.div({
    enter: { opacity: 1 },
    exit: { opacity: 0 },
});
const App: React.FC = () => {
    const { theme, Themes, toggleTheme } = useTheme();
    const urlPrefix = process.env.NODE_ENV === 'development' ? '' : '/real-time-chat';
    return (
        <div className={theme === Themes.dark ? 'theme-dark' : 'theme-light'}>
            <Route
                render={({ location }) => (
                    <div className="container">
                        <span className="notch" />
                        <div className="welcome">
                            <h1 className="title">Real-time chat</h1>
                            <div className="divider">
                                <span className="hr" />
                                <Link
                                    className="link"
                                    to={`${
                                        location.pathname === `${urlPrefix}/chat`
                                            ? `${urlPrefix}/info`
                                            : `${urlPrefix}/chat`
                                    }`}>
                                    <MorphingIcon
                                        width={18}
                                        height={18}
                                        icon={
                                            location.pathname === `${urlPrefix}/chat`
                                                ? `info`
                                                : `chat`
                                        }
                                    />
                                </Link>
                            </div>
                        </div>
                        <PoseGroup>
                            <RouteContainer
                                key={`routerContainer${location.key}`}
                                className="routerContainer">
                                <Switch location={location} key={'switch'}>
                                    <Route path={`${urlPrefix}/chat`} component={Chat} key="chat" />
                                    <Route path={`${urlPrefix}/info`} component={Info} key="info" />
                                    <Route component={Chat} key="default" />
                                </Switch>
                            </RouteContainer>
                        </PoseGroup>
                        <div className="footer">
                            <ToggleButton
                                checked={theme === Themes.light}
                                toggle={toggleTheme}
                                checkedIcon="sun"
                                uncheckedIcon="moon"
                                big
                            />
                        </div>
                    </div>
                )}
            />
        </div>
    );
};

export default App;
