import React, { useState, useRef, useEffect, useContext } from 'react';
import { useLocation } from 'react-router-dom';
import posed, { PoseGroup } from 'react-pose';
import { Message, MessageContext } from '../../contexts/messages';
import moment from 'moment';
import './AnimatedList.scss';

interface Props {
    status: string;
    messages: Array<Message>;
}

const AnimatedItem = posed.span({
    exit: { rotateX: '90deg', opacity: 0 },
    enter: { rotateX: '0deg', opacity: 1 },
});

const AnimatedList: React.FC<Props> = ({ messages, status }) => {
    const [selected, setSelected] = useState<string>();
    const location = useLocation();
    const { name } = useContext(MessageContext);
    const listRef = useRef(null);
    const onSelectMessage = (id: string) => {
        if (selected === id) setSelected('id');
        else setSelected(id);
    };
    useEffect(() => {
        console.log(location.pathname);
        console.log({ ...listRef });
        console.log(messages.length);
        if (messages.length && listRef) {
            listRef.current.scrollIntoView({ behavior: 'smooth' });
            console.log('...');
        }
    }, [messages, listRef]);
    return (
        <div className="list">
            <PoseGroup>
                {messages.map((message: Message) => {
                    return (
                        <AnimatedItem
                            className={`message ${selected === message.id ? 'selected' : ''} ${
                                name === message.user ? 'own' : ''
                            }`}
                            key={`message${message.id}`}
                            onClick={() => onSelectMessage(message.id)}>
                            <div className={`content `}>
                                <p className="header">
                                    <span className="user">{message.user}</span>
                                    <span className="date">
                                        {moment(message.date).format('HH:mm')} Hs.
                                    </span>
                                </p>
                                <p className="body">{message.message}</p>
                            </div>
                        </AnimatedItem>
                    );
                })}
            </PoseGroup>
            <span className="dummy" ref={listRef} />
            {status === 'fetching' && <p>fetching</p>}
        </div>
    );
};

export default AnimatedList;
