import React, { useContext } from 'react';
import { Redirect, useLocation } from 'react-router-dom';
import { AnimatedList, ChatInput } from '..';
import './Chat.scss';
import { MessageContext } from '../../contexts/messages';
const Chat: React.FC = () => {
    const { push, messages, status, name } = useContext(MessageContext);
    const urlPrefix = process.env.NODE_ENV === 'development' ? '' : '/real-time-chat';
    const location = useLocation();
    return true ? (
        <div className="chat">
            <div className="intro">
                <p>
                    Hi! From now your name is <span className="name">{name}</span> ðŸ˜Š
                </p>
                <p>(Open another window to see this real-time chat working)</p>
            </div>
            <AnimatedList messages={messages} status={status} />
            <ChatInput push={push} />
        </div>
    ) : (
        <Redirect
            to={{
                pathname: `${urlPrefix}/info`,
                state: { from: location },
            }}
        />
    );
};

export default Chat;
